* {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
}

a:link,
a:visited {
    text-decoration: none;
    color: black;
}

a.active {
    color: white;
}

h1,
footer,
.nav ul {
    padding: 0.5rem;
}

h4,
li {
    margin: 0.5rem 0;
}

hr {
    display: none;
    margin: 1rem 0;
    border: 0;
    border-top: 1px solid #ccc;
}

input[type=text] {
    width: 120px;
}

.d-block {
    display: block;
}

.container {
    display: flex;
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
}

.nav {
    min-height: 300px;
    background-color: #4285F4;
}

.nav ul {
    list-style: none;
}

.contents {
    flex: 1;
    padding: 1rem;
}

.table-search {
    border: 1px solid rgb(118, 118, 118);
    border-collapse: collapse;
    text-align: center;
}

.table-search th,
.table-search td {
    padding: 0.2rem;
}

.table-search td {
    border-top: 1px solid rgb(118, 118, 118);
    min-width: 100px;
}
